<template>
  <div :class="['home', isBlurring ? 'blurring' : '']">
    <div v-for="editor in editors"
      :key="editor.uuid"
      class="editor" :class="{ active: activeEditorUUID === editor.uuid}"
      @click="setActiveEditor(editor.uuid)">
      <div class="actions">
        <span class="button" v-if="editors.length > 1" @click="removeEditor(editor.uuid)">×</span>
      </div>
      <textarea
        :id="`t-${editor.uuid}`"
        :placeholder="placeholder"
        v-model="editor.note"
        @input="onInput"
        @keyup.esc="unfocus(editor.uuid)"
        @blur="unfocus(editor.uuid)"></textarea>
    </div>
  </div>
</template>

<script>
import Events from '@/events'
import uuid from '@/uuid'
import draft from '@/draft'

export default {
  name: 'home',

  data () {
    return {
      placeholder: 'Write something...',
      isBlurring: false,
      editors: [],
      activeEditorUUID: 0
    }
  },

  methods: {
    unfocus (_uuid) {
      this.$el.querySelector(`#t-${_uuid}`).blur()
      this.setActiveEditor(0)
    },

    setActiveEditor (_uuid) {
      this.activeEditorUUID = _uuid
    },

    newEditor () {
      this.editors.push({ uuid: uuid() })
    },

    removeEditor (_uuid) {
      this.editors = this.editors.filter((editor) => {
        return editor.uuid !== _uuid
      })
      draft.removeDraft(_uuid)
    },

    saveDrafts () {
      const allDrafts = this.editors.map((editor) => {
        return { uuid: editor.uuid, note: editor.note }
      })
      draft.saveAllDrafts(allDrafts)
    },

    onInput () {
      this.$nextTick(() => {
        this.saveDrafts()
      })
    }
  },

  mounted () {
    Events.$on('blurring', (state) => {
      this.isBlurring = state
    })
    Events.$on('vertical-split-window', () => {
      this.newEditor()
    })
    Events.$on('save-drafts', this.saveDrafts)

    // Load drafts from previous sessions
    const allDrafts = draft.loadAllDrafts()
    if (allDrafts.length > 0) {
      allDrafts.map((draft) => {
        this.editors.push({ uuid: draft.uuid, note: draft.note })
      })
    } else {
      this.editors.push({ uuid: uuid(), note: '' })
    }
  }
}
</script>

<style lang="stylus">
.home
  &.blurring
    filter: blur(5px)
  padding-top 50px
  display flex
  flex 1
  justify-content center

  .editor
    display flex
    flex 1
    width 100%
    max-width 640px
    position relative
    padding 1em
    border-top 2px solid transparent

    &:not(:first-child)
      border-left 2px solid #eee

    &.active:not(:only-child)
      border-top 2px solid #DD4C4F

    .actions
      opacity 0
      position absolute
      top 0
      right 5px
      font-size 1.2em
      .button
        cursor pointer
        font-size 1.5em
        line-height 1em
        color #aaa
    &.active .actions
      opacity 1

    textarea
      font-family 'Open Sans Condensed'
      width 100%
      border none
      overflow auto
      outline none
      -webkit-box-shadow none
      -moz-box-shadow none
      box-shadow none
      background-color transparent
      font-size 1em
      resize none

      &:not(:focus)
        color rgba(0, 0, 0, .5)
</style>



