<template>
  <div class="header">
    <div class="logo" @click="logoClick">z0ey</div>
  </div>
</template>

<script>
import Events from '@/events'

export default {
  name: 'header-bar',

  methods: {
    logoClick () {
      Events.$emit('logoClick')
    }
  }
}
</script>

<style lang="stylus">
.header
  width 100%
  height 50px
  position fixed
  display flex
  flex 1
  justify-content center
  background-color transparent

  .logo
    cursor pointer
    // border-bottom 2px solid #DD4C4F
    font-family 'Sacramento', 'Damion', cursive
    font-size 2em
    line-height 50px

    &:hover
      text-shadow 1px 1px
</style>



